/* eslint-disable jsx-a11y/anchor-has-content */
import styled, {
  themeGet,
  space,
  SpaceProps,
  device,
  css,
} from '@sieloapp/shared/styled';
import { CardHeader, CardFooter, Card } from '@sieloapp/components';

export const StyledHeader = styled(({ ...props }) => <CardHeader {...props} />)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 38px;
`;

export const StyledHeaderRight = styled.div`
  font-size: 18px;
  display: flex;
`;

export const StyledIcon = styled(({ m, ml, mr, mt, mb, ...rest }) => (
  <a {...rest} />
))<SpaceProps>`
  color: ${themeGet('colors.text3')};
  line-height: 0;
  ${space};
`;

export const StyledListMiddle = styled.div`
  ${device.small} {
    padding-left: 10px;
  }
`;

export const StyledListTitle = styled.p`
  font-weight: 500;
  margin-bottom: 0px;
  font-size: 13px;
`;

export const StyledListText = styled.small`
  color: ${themeGet('colors.text3')};
  font-size: 12px;
  margin-bottom: 0px;
`;

export const StyledListEnd = styled.div`
  margin-left: auto;
  text-align: right;
`;

export const StyledListTrans = styled.p`
  font-weight: 500;
  margin-bottom: 0px;
  font-size: 13px;
`;

interface ISourceStatus {
  $source: 'web' | 'database' | 'ckan' | 'api';
}

export const StyledListStatus = styled.small<ISourceStatus>`
  font-size: 12px;
  ${({ $source }) =>
    $source === 'database' &&
    css`
      color: ${themeGet('colors.success')};
    `}
  ${({ $source }) =>
    $source === 'api' &&
    css`
      color: ${themeGet('colors.info')};
    `}
    ${({ $source }) =>
    $source === 'web' &&
    css`
      color: ${themeGet('colors.danger')};
    `}
`;

export const StyledFooter = styled(({ ...props }) => <CardFooter {...props} />)`
  text-align: center;
  font-size: 13px;
`;

export const StyledFooterLink = styled.a`
  color: ${themeGet('colors.text3')};
  i {
    margin-left: 5px;
  }
`;

export const StyledCard = styled(Card)`
  background-clip: none;
  border: none;
  border-radius: 0;
`;

export const StyledDataSourceItem = styled.div`
  position: relative;
  cursor: pointer;

  &:hover::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.1);
  }
`;
