import { ListGroup, ListGroupItem, SectionTitle } from '@sieloapp/components';
import Item from './item';
import {
  StyledHeader,
  StyledFooter,
  StyledFooterLink,
  StyledCard,
  StyledDataSourceItem,
} from './style';
import { ISearchResults } from '@sieloapp/shared/types';
import { MouseEventHandler } from 'react';

interface DataSourceProps {
  data: ISearchResults;
  onDataSourceClick: (id: string) => void;
}

const DataSources: React.FC<DataSourceProps> = ({
  data,
  onDataSourceClick,
}) => {
  const onClick =
    (id: string): MouseEventHandler<HTMLDivElement> =>
    () => {
      onDataSourceClick(id);
    };

  return (
    <StyledCard height="100%">
      <StyledHeader>
        <SectionTitle title="Data Sources" />
      </StyledHeader>
      <ListGroup flush>
        {Array.isArray(data.searchResults) &&
          data.searchResults.map((resp) => (
            <StyledDataSourceItem
              key={resp.widget.id}
              onClick={onClick(resp.widget.id)}
            >
              <ListGroupItem display="flex" px={[null, '20px']}>
                <Item
                  id={resp.widget.id}
                  type={resp.widget.type}
                  SE={resp.widget.SE}
                  title={resp.widget.title}
                  description={resp.widget.description}
                  count={resp.widget.count}
                  source={resp.widget.source}
                  icon={resp.widget.icon}
                  bg={resp.widget.bg}
                />
              </ListGroupItem>
            </StyledDataSourceItem>
          ))}
      </ListGroup>
      <StyledFooter>
        <StyledFooterLink href="/invoice">
          Prev | Next <i className="icon ion-md-arrow-down mg-l-5" />
        </StyledFooterLink>
      </StyledFooter>
    </StyledCard>
  );
};

export default DataSources;
