import Content from '../layouts/content';
import SearchContainer from '../containers/main-search';
import SearchResultsContainer from '../containers/search-results';
import SEO from '../components/seo';
import { useState } from 'react';
import {
  IDataSources,
  IGoogleSearchresponse,
  ISearchResults,
} from '@sieloapp/shared/types';
// import { Spinner } from '@sieloapp/components';

const defaultState: ISearchResults = {
  searchResults: [
    {
      widget: {
        id: '',
        type: '',
        SE: '',
        title: '',
        description: '',
        count: 0,
        source: 'web',
        icon: '',
        bg: '',
      },
      results: [],
      page: 0,
    },
  ],
};

const widgets: IDataSources[] = [
  {
    id: 'chebi',
    type: 'google',
    SE: 'c0a03c1c318484836',
    title: 'CHEBI ontologies',
    description: 'Chemical Entities of Biological Interest',
    count: 0,
    source: 'web',
    icon: 'fas fa-flask',
    bg: 'teal',
  },
  {
    id: 'bioportal',
    type: 'google',
    SE: 'a21e51c727a914a2f',
    title: 'BioPortal ontologies',
    description: 'BioPortal ontologies',
    count: 0,
    source: 'web',
    icon: 'fas fa-dna',
    bg: 'orange',
  },
];

const SearchPage: React.FC = () => {
  const [data, setData] = useState<ISearchResults>(defaultState);
  const [searching, setSearching] = useState<boolean>(false);
  const [searchResults, setSearchResults] = useState<boolean>(false);

  const googleSearch = async (searchQuery: string, googleSE: string) => {
    const GoogleApiKey = process.env.REACT_APP_GOOGLE_SEARCH_SITE_KEY as string;
    // const GoogleSE = process.env.REACT_APP_GOOGLE_SEARCH_SE_ID as string;
    try {
      const response = await fetch(
        'https://customsearch.googleapis.com/customsearch/v1/siterestrict?key=' +
          GoogleApiKey +
          '&cx=' +
          googleSE +
          '&q=' +
          searchQuery,
        {
          headers: {
            Accept: 'application/json',
          },
        },
      );
      return (await response.json()) as IGoogleSearchresponse;
    } catch (error) {
      console.error(error);
    }
  };

  const handleSearch = async (searchQuery: string) => {
    if (searchQuery === '') return;
    let currentResultsCount = 0;
    setData(defaultState);
    setSearching(true);
    const promises = widgets.map(
      async (widget: {
        type: string;
        SE: string;
        id: any;
        title: any;
        description: any;
        source: any;
        icon: any;
        bg: any;
      }) => {
        if (widget.type === 'google') {
          try {
            const jsonData: IGoogleSearchresponse = (await googleSearch(
              searchQuery,
              widget.SE,
            )) as IGoogleSearchresponse;
            currentResultsCount = parseInt(
              jsonData.searchInformation.totalResults,
              10,
            );
            if (currentResultsCount === 0) return;
            setData((prevState: ISearchResults) => ({
              // ...prevState,
              searchResults: [
                ...prevState.searchResults.filter(
                  (result) => result.widget.count !== 0,
                ),
                {
                  widget: {
                    id: widget.id,
                    type: widget.type,
                    SE: widget.SE,
                    title: widget.title,
                    description: widget.description,
                    count: currentResultsCount,
                    source: widget.source,
                    icon: widget.icon,
                    bg: widget.bg,
                  },
                  results: jsonData.items,
                  page: jsonData.queries.nextPage
                    ? jsonData.queries.nextPage[0].startIndex / 10 - 1
                    : 0,
                },
              ],
            }));
            setSearchResults(true);
          } catch (error) {
            setSearchResults(false);
            console.error(error);
            setSearching(false);
          }
        }
      },
    );

    await Promise.all(promises);
    setSearching(false);

    // try {
    //   const response = await fetch(
    //     'https://data.bioontology.org/search?q=' + searchQuery,
    //     {
    //       headers: {
    //         Authorization: 'apikey token=' + apiKey,
    //       },
    //     },
    //   );
    //   const jsonData: IBioPortalresponse = await response.json();
    //   setData(jsonData);
    //   setSearching(false);
    // } catch (error) {
    //   console.error(error);
    //   setSearching(false);
    // }
  };

  const handleClear = () => {
    setSearching(false);
    setSearchResults(false);
    setData(defaultState);
  };

  return (
    <>
      <SEO />
      <Content fullHeight align="center" flex={!searchResults}>
        <SearchContainer
          onSearch={handleSearch}
          onClear={handleClear}
          searching={searching}
        />
        {searchResults && <SearchResultsContainer data={data} />}
      </Content>
    </>
  );
};

export default SearchPage;
