import { Col, Row } from '@sieloapp/components';
// import Transaction from '../../../components/dashboard-one/transaction';
// import Customers from '../../../components/dashboard-one/customers';
// import RealTimeSales from '../../../components/dashboard-one/real-time-sales';
import DataSources from '../../components/syndicated-search/data-sources';
import Results from '../../components/syndicated-search/results';
import { StyledContent } from './style';
import { ISearchResults } from '@sieloapp/shared/types';
import { useState } from 'react';
// import data from '@sieloapp/shared/data/dashboard-one/real-time-sales';

interface SearchResultsContainerProps {
  data: ISearchResults;
}

const SyndicatedSearch: React.FC<SearchResultsContainerProps> = ({ data }) => {
  const [idToShow, setIdToShow] = useState<string>('');

  const handleDataSourceClick = (id: string) => {
    setIdToShow(id);
  };

  return (
    <StyledContent>
      <Row>
        <Col md={4} xl={4} mt="10px">
          <DataSources data={data} onDataSourceClick={handleDataSourceClick} />
        </Col>
        <Col md={8} xl={8} mt="10px">
          <Results data={data} id={idToShow} />
        </Col>
      </Row>
    </StyledContent>
  );
};

export default SyndicatedSearch;
