import { Avatar, AvatarInitial } from '@sieloapp/components';
import { IDataSources } from '@sieloapp/shared/types';
import {
  StyledListMiddle,
  StyledListTitle,
  StyledListText,
  StyledListEnd,
  StyledListTrans,
  StyledListStatus,
} from './style';

const DataItem = ({
  title,
  description,
  count,
  source,
  icon,
  bg,
}: IDataSources) => {
  const opacity = 1;
  return (
    <>
      <Avatar display={['none', 'block']}>
        <AvatarInitial bg={bg} opacity={opacity}>
          <i className={icon} />
        </AvatarInitial>
      </Avatar>
      <StyledListMiddle>
        <StyledListTitle>{title}</StyledListTitle>
        <StyledListText>{description}</StyledListText>
      </StyledListMiddle>
      <StyledListEnd>
        <StyledListTrans>{count}</StyledListTrans>
        <StyledListStatus $source={source}>
          {source === 'api' && 'API'}
          {source === 'database' && 'Database'}
          {source === 'web' && 'External'}
          {source === 'ckan' && 'CKAN'}
        </StyledListStatus>
      </StyledListEnd>
    </>
  );
};

export default DataItem;
